

body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #ffffff;
  color: #000000;
}

.app-container {
  width: 100%;
  height: 100%;
}

h1, h2, h3 {
  margin: 10px 0;
}

.input-group {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-id-input {
  padding: 8px;
  font-size: 16px;
  width: 200px;
  text-align: center;
}

.primary-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  background: linear-gradient(135deg, rgb(0, 122, 223), rgb(0, 236, 188));
  color: #fff;
  border: none;
  cursor: pointer;
}

.button-start {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  background: linear-gradient(135deg, rgb(0, 122, 223), rgb(0, 236, 188));
  color: #fff;
  border: none;
  cursor: pointer;
}

.primary-button:hover {
  opacity: 0.9;
}

.questions-list {
  width: 100%;
  max-width: 600px;
}

.question-item {
  margin-bottom: 20px;
}

.question-text {
  font-weight: bold;
  margin-bottom: 5px;
}

.options-list {
  list-style-type: none;
  padding-left: 20px;
}

.options-list li {
  margin: 3px 0;
}

.feud-list p {
  margin: 3px 0;
}

.slide-container{
    align-items: center;
    /* background-image: url(/static/media/trivia-app.f8c4af2â€¦.PNG); */
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    box-sizing: border-box;
    color: #fff;
    display: flex;
    flex-direction: column;
    margin: 0;
    max-width: 100%;
    min-height: 40vh;
    padding: 20px;
}

.welcome-image {
  width: 70vh;
  padding: 10px;
  color: #fff;
}

.breaktime-image {
  width: 70vh;
  padding: 10px;
  color: #fff;
}

.sec-container {
  background-image: url('/public/Slide3.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  padding: 10px;
  color: #fff;
}

.lastpage-container {
  background-image: url('/public/trivia-app.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.round1-container {
  background-image: url('/public/Slide4.PNG');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  padding: 10px;
  color: #fff;
}

.section-image{
  margin-bottom: auto;
  width: 15%;
  margin: 10px;
  padding: 0;
  line-height: 1;
}

.lastsec-image{
  width: 70vh;
  padding: 10px;
  color: #fff;
}

.sectionNum{
  font-size: 8rem;
  font-weight: bold;
  margin: 0;
  padding: 0;
  line-height: 1;
}

.catName{
  font-size: 3rem;
  font-weight: bold;
  margin: 5rem;
  padding: 0;
  line-height: 1;
}

.dataTitle{
  height: 10px;
  font-size: 3rem;
  font-weight: bold;
  color: #be7ebb;
  margin: 0;
  padding: 0;
  line-height: 1;
}

.slide-container h3, h1{
  margin-top: 0rem;
}

.question-text{
  font-size: 16px;
}

.input-answer{
  width: 80%;
  padding: 10px;
  border-radius: 10px;
}

.audio-question button {
  display: inline-block;
}

/*Rules Page */
/* styles.css */

.rules-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 0 10px;
  box-sizing: border-box;
}

.rules-title {
  font-size: 36px;
  font-weight: bold;
  color: #addbe4;
  margin: 0;
}

.rules-logo {
  height: 60px;
  object-fit: contain;
}

/* Rules List */
.rules-container ol {
  padding-left: 20px;
}

.rules-container li {
  font-size: 20px;
  margin-bottom: 18px;
  line-height: 1.5;
}

/* Fixed Button */
.fixed-next-button {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 20px;
  padding: 12px 32px;
}

/* Mobile Responsive */
@media (max-width: 600px) {
  .rules-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .rules-title {
    font-size: 28px;
  }

  .rules-logo {
    height: 48px;
    align-self: flex-end;
  }

  .rules-container li {
    font-size: 18px;
  }

  .fixed-next-button {
    width: 90%;
    font-size: 18px;
  }
}

/* For Multiple Choice */
.answer-options {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
  justify-content: center;
}

.answer-option {
  color: #be7ebb;
  white-space: nowrap;
  font-size: 1.1rem;
}

/* Stack vertically on small screens or if text wraps */
@media (max-width: 600px) {
  .answer-options {
    flex-direction: column;
    align-items: flex-start;
  }

  .answer-option {
    white-space: normal;
  }
}

